import { HTMLToJsonMLRule, YUQUE } from '@ali/4ever-cangjie';
import parseFromYuQue from './parseFromYuQue';
import parseFromWeb from './parseFromWeb';

export default function htmlToJsoML(pasteFrom): HTMLToJsonMLRule {
  if (pasteFrom === YUQUE) {
    return parseFromYuQue();
  }
  return parseFromWeb();
};
